import request from "@/api/request";
import {HResponse, getNonce, IUserInfo, userLogin, Edit} from "@/api/interface";
export default {
    /**
     * 获取nonce
     */
    getNonce(data: getNonce){
        return request<HResponse>({
            url:'/account/nonce',
            method:'post',
            loading:true,
            data
        })
    },
    /**
     * 用户登录
     */
    userLogin(data: userLogin){
        return request<HResponse>({
            url:'/account/auth',
            method:'post',
            loading:true,
            data
        })
    },
    /**
     * 获取用户信息
     */
     getUserInfo(){
        return request<HResponse<IUserInfo>>({
            url:'/account/infos',
            method:'get',
            loading:true
        })
    },
    /**
     * 编辑昵称
     */
    userEdit(data: Edit){
        return request<HResponse>({
            url:'/account/edit',
            method:'post',
            loading:true,
            data
        })
    },
    refreshToken(data){
      return request<HResponse>({
          url:'/account/refresh',
          method:'post',
          loading:true,
          data
      })
    }
}