import request from "@/api/request";
import {HResponse, userAssets} from "@/api/interface";
export default {
    /**
     * 获取用户余额
     */
    getBalance(data){
        return request<HResponse<userAssets>>({
            url:'/wallet/currency/get_balance',
            method:'post',
            loading:true,
            data
        })
    },

}