// 语言
import { createI18n } from 'vue-i18n';	// 引入vue-i18n组件
// import { 引入的组件 export 出来的 变量} from 'vue-i18n'
  // 注册i8n实例并引入语言文件
 const i18n = createI18n({
      locale: localStorage.getItem('lang') || 'en',    // 语言标识 localStorage.getItem('lang') ||
      messages: {
        cn: require('./cn'),    // 中文语言包
        en: require('./en'),    // 英文语言包
      },
  });
  export default i18n;
