import axios, {AxiosResponse, AxiosRequestConfig} from 'axios';
import {AUCTION_API_HOST, ACCOUNT_API_HOST} from './config';
import {ElMessage, ElLoading} from 'element-plus'
import store from '@/store'
import router from "@/router";

export const service = axios.create({
    baseURL: AUCTION_API_HOST,
    // withCredentials: true,
    timeout: 5000,
});
let loading: any = null;
// request拦截器
service.interceptors.request.use((config: AxiosRequestConfig) => {
    //TODO loading动画
    if (config.loading) {
        loading = ElLoading.service({
            lock: true,
            text: 'Loading',
            background: 'rgba(0, 0, 0, 0.7)',
        })
    }
    if (store.state.auth.token) {
        if (!config.headers) {
            config.headers = {};
        }
        if(store.state.auth.token.loginTime && ((new Date().getTime() - store.state.auth.token.loginTime)/ 1000 > 10 * 60)){
            // store.dispatch('auth/refreshToken') //刷新token
            console.log('++++++refresh_token',store.state.auth.token.refresh_token)
            axios.post(AUCTION_API_HOST + "/account/refresh",
                {refresh_token:store.state.auth.token.refresh_token},
                {
                    headers:{'Authorization':`Bearer ${store.state.auth.token.access_token}`}
                }).then(res=>{
                if (res.code == 0) {
                    const loginData = {...res.data,loginTime:new Date().getTime()}
                    store.commit('SET_TOKEN', loginData)
                }
            })
        }
        config.headers['Authorization'] = `Bearer ${store.state.auth.token.access_token}`
        // config.headers['Authorization'] = 'Bearer eyJ1c2VyaWQiOjE0NjE5Nzc1NTU0Mzg0MDc2ODB9.898b7179dd9d0e17ccaad00e51b5c7c0'
    }
    return config;
}, (error) => {
    window.console.log(error);
    return Promise.reject(error);
});


service.interceptors.response.use(
    async (response: AxiosResponse) => {
        // ElLoading.service().close()
        if (loading) {
            setTimeout(() => {
                loading.close();
            }, 200);
        }
        const res = response.data
        if (res.code !== 0 && res.code !== 200) {
            if (res.code === 401 || res.code === 402 || res.code === 403) {
                //TODO 登录失效的跳转
                localStorage.removeItem('token')
                store.commit('auth/SET_ISLOGIN', false)
                store.commit('auth/SET_TOKEN', '')
                router.push({path:'/login'})
                return
            }
            // return Promise.reject(new Error(res.message || 'Error'))
        }

        return response.data
    },
    ((error: any) => {
        // ElLoading.service().close()
        if (loading) {
            setTimeout(() => {
                loading.close();
            }, 200);
        }
        console.log(error)
        if (error && error.response) {
            //TODO 错误信息状态码
            switch (error.response.status) {
                case 400:
                    error.message = '请求错误'
                    break;
                case 401:
                    error.message = '权限错误'
                    break;
                default:
                    error.message = `请求异常： ${error.message}`
                    console.error()
            }
            ElMessage.error(error.message)
        } else {
            if (error.message == 'Network Error') {
                error.message == '网络异常'
            }
        }
        ElMessage.error('Oops, this is a error message.')
        return Promise.reject(error)
    })
)

export default service;
