import { Module } from 'vuex'
import { IGlobalState } from '@/store/index'
import { IAuthState } from '@/store/modules/Auth/interface'
import * as Types from '@/store/modules/Auth/types'
import Api from "@/api";

const state: IAuthState = {
  userInfo: {},
  isLogin: false,
  token: {},
}

const login: Module<IAuthState, IGlobalState> = {
  namespaced: true,
  state,
  mutations: {
    ['SAVE_USER_INFO'](state, data) {
      state.userInfo = data
    },
    ['SET_ISLOGIN'](state, data) {
      state.isLogin = data
    },
    ['SET_TOKEN'](state, data) {
      console.log('设置token',data)
      state.token = data
    }
  },
  actions: {
    async [Types.SAVE_USER_INFO]({ commit }, data) {
      return commit(Types.SAVE_USER_INFO, data)
    },
    async ['SET_ISLOGIN']({ commit }, data) {
      return commit('SET_ISLOGIN', data)
    },
    async ['SET_TOKEN']({ commit }, data) {
      return commit('SET_TOKEN', data)
    },
    async refreshToken({ commit }, data){
      const params = {
        userid:this.state.auth.userInfo.id,
        refresh_token:this.state.auth.token.refresh_token
      }
      console.log('刷新token')
      const res = await Api.refreshToken(params);
      if (res.code == 0) {
        commit('SET_TOKEN', res.data)
      }
    }
  }
}

export default login
